<template>
	<div id="forget" :style="img">
		<div id="head">
			<i @click="goBack"  class="el-icon-arrow-left"></i>
			
		</div>
		<ul>
			<li>
				<input v-on:keyup="getCodeBg" v-model="phoneNumber" id="phone" class="inp" type="text" placeholder="">
			</li>
			<li>
				<input v-model="code" id="code" class="inp" type="text" placeholder=""><button disabled @click="getCode" id="getcode" class="inp"></button>
			</li>
			<li>
				<button @click="findPass" id="next" class="inp"></button>
			</li>
		</ul>
		<div id="foot">
			<p>?&nbsp;&nbsp;&nbsp;&nbsp;<a href="tel:18745708406"></a></p>
		</div>
	</div>
</template>

<script>
	var phoneCode = ''
	export default{
		phoneCode,
		data(){
			return{
				img:{background:'url(' + require('../../static/img/kongmingdeng.jpg') + ')'},
				phoneNumber:'',
				code:'',
				
			}
		},
		methods:{
			// 
			goBack:function(){
				this.$router.push({path:'/login'})
			},
			// 
			findPass:function(){
				const reg = /^[1][3,4,5,7,8][0-9]{9}$/;
				if (this.phoneNumber=='') {
					this.$message({
						message:'',
						type:'error',
						center:true
					});
				}
				else if(reg.test(this.phoneNumber)!=true){
					this.$message({
						message:'',
						type:'error',
						center:true
					});
				}
				else if (this.code=='') {
					this.$message({
						message:'',
						type:'error',
						center:true
					});
				}
				else if (this.code!=phoneCode) {
					this.$message({
						message:'',
						type:'error',
						center:true,
					})
				}
				else if (this.code==phoneCode) {
					this.$message({
						message:'',
						type:'success',
						center:true,
						duration:5000
					})
					this.$router.push({path:'/register'})
				}
			},
			// 
			getCodeBg:function(){
				const reg = /^[1][3,4,5,7,8][0-9]{9}$/;
				const getCodeBg = document.querySelector('#getcode');
				if (reg.test(this.phoneNumber)==true) {
					getCodeBg.style.background='#261b88';
					getCodeBg.removeAttribute('disabled')
				}
				else{
					getCodeBg.style.background='';
					getCodeBg.setAttribute('disabled','disabled')
				}
			},
			// 
			getCode:function(){
				const getCodeBg = document.querySelector('#getcode');
				getCodeBg.setAttribute('disabled','disabled')
				let time = 15;
				const T = setInterval(function(){
					time --;
					getCodeBg.innerText=time+'s'
					if (time == 10) {
						getCodeBg.removeAttribute('disabled')
						time = 15
						clearInterval(T);
						getCodeBg.innerText=''
						phoneCode = ''
						// 
						for (var i = 0; i < 5; i++) {
	    			    	var Rand =Math.floor(Math.random()*10);
	    			    	phoneCode +=Rand
					    }
					    alert(phoneCode)

					}
				},1000)
			},


		}
	}
</script>

<style lang="scss" scoped>
	#forget{
		height:100%;
		#head{
			height:2rem;
			line-height:3rem;
			color:#fff;
			font-size:1rem;
			font-weight:bold;
			i{
				position:absolute;
				left:2rem;
				top:1rem;
				font-weight:bold;
			}
		}
		#foot{
			margin-top: 14rem;
			p{
				color:#fff;
				font-size:.8rem;
				a{
					color:#0c5593 !important;
				}
			}
		}
		ul {
			padding-top: 6rem;
			li{
				display:block;
				width:90%;
				margin: 0 auto;
				margin-top: 2rem;
				input{
					box-shadow:none;
				}
				.inp{
					padding:.6rem .4rem;
					color:#fff;
				}
				#phone{
					text-indent:10px;
					width:100%;
					border-radius:12px;
					border:1px solid #f5f5f5;
					text-align:center;
				}
				#code{
					text-indent:10px;
					width:70%;
					border-radius:12px 0 0 12px;
					border-right:none !important;
					border:1px solid #f5f5f5;
				}
				#getcode{
					width:30%;
					border-radius:0 12px 12px 0;
					text-align:center;
					border:1px solid #f5f5f5;
					padding:.52rem .8rem;
					letter-spacing:.12rem;
					line-height:1rem;

				}
				#next{
					width:98%;
					border-radius:12px;
					background-color:rgba(13,12,12,0.7);
					font-size:.9rem;
				}
			}

		}
	}
</style>









