// import $ from './jquery-3.1.1.min.js'
var $ = require('./jquery-3.1.1.min.js')
module.exports= function(){
	$(function(){
		//1(2-5)
		$(".font_inner li:eq(0)").clone(true).appendTo($(".font_inner"));//()
		var liHeight = $(".swiper_wrap").height();//li
		//lili(LiLi)
		var totalHeight = ($(".font_inner li").length *  $(".font_inner li").eq(0).height()) -liHeight;
		$(".font_inner").height(totalHeight);//ul
		var index = 0;
		var autoTimer = 0;//
		var clickEndFlag = true; //
		function tab(){
			$(".font_inner").stop().animate({
				top: -index * liHeight
			},1000,function(){
				clickEndFlag = true;//true
				if(index == $(".font_inner li").length -1) {
					$(".font_inner").css({top:0});
					index = 0;
				}
			})
		}

		function next() {
			index++;
			if(index > $(".font_inner li").length - 1) {//indexLiindex0
				index = 0;
			}
			tab();
		}
		//
		autoTimer = setInterval(next,1500);
		$(".font_inner a").hover(function(){
			clearInterval(autoTimer);
		},function() {
			autoTimer = setInterval(next,1500);
		})

		// 
	})
}



